<template>
  <div class="wrap">
      <h2 class="iconfont icon-wenzhang"> 最新文章</h2>
      <ul>
          <li v-for="(item,index) in article">
              <span>{{index+1}}</span>
              <span @click="toarticle(item._id)">{{item.title}}</span>
          </li>
      </ul>
  </div>
</template>

<script>
export default {
    name:'Newpost',
    data(){
        return{
            article:[]
        }
    },
    methods:{
        async getArticles() {
            let { data } = await this.$axios({
                method: "GET",
                url: "/get/article",
            });
            // console.log(data);
            if (data.code) return this.$message.error(data.message); //没有文章时/报错，直接return
            this.article = data.data; //所有文章数据给allArticleData
        },
        toarticle(item){
            this.$router.push(`/article/${item}`)
            
        }
    },
    created(){
        this.getArticles();
    }
}
</script>

<style lang="less" scoped>
    .wrap{
        width: 100%;
        user-select: none;
        background-color: hsla(0,0,0,.6);
        border-radius: 10px;
        h2{
            color: white;
            font-size: 17px;
            line-height: 40px;
            width: 80%;
            height: 40px;
            margin: 0 auto;
            padding-left: 20px;
            font-weight: bold;
            border-bottom: 1px solid #e0e0e0;
        }
        ul{
            margin-top: 10px;
            li{
                height: 40px;
                line-height: 40px;
                cursor: pointer;
                font-size: 18px;
                font-family: "微软雅黑";
                border-radius: 10px;    
                color: #e0e0e0;
                &:hover{
                    background: white;
                    transition: .1s;
                }
                span{
                    display: inline-block;
                    margin-left: 20px;
                    font-weight: bold;
                }
                &:hover span{
                    margin-left: 30px;
                    transition: .5s;
                    color: black;
                }
            }
        }
    }
</style>