          你好，欢迎光临我的博客，简单的做下自我介绍吧，我是一面web前端开发者(做这个博客的时候我还处于初级阶段)，来自四川内江，原先写了一个博客感觉不是很好看我就重写了一个，目前来看哈，就目前以我的审美感觉这个博客自我感觉良好，给人一种小清新的感觉。
        </p>
        <h3>下面简单的介绍下这个博客的功能吧！</h3>
        <p>博客目前使用的是Vue
          + Element + Node + Express +
          MongoDB搭建的</p>
          <p>实现了各种简单的交互,列如:登录注册,留言交流区，文章发表区(只限于管理员),浏览相册,个人信息修改,反馈等。</p>
        <h3>说说自己的学习心得！</h3>
          <p>我是在高三第一次接触的编程,也是缘分吧，第一次看见编程是一个不是很熟的人在写代码,看都看不懂总觉得很酷,那时就决定我也要学(纯粹是为了装逼而学)
            第一门语言学的是Java,当时就郁闷咋这么难,然后学了半年直接把我难住(当时都是随便学的压根没有一个准确的路线学得很恼火),接着接触了JavaScript我才发现了新大陆原来编程可以这么简单,直接转型扔了Java投身于JavaScript,
            学了大概也是半年也是杂七杂八的内容,学完的感觉就是一种懵懂懵懂的状态,感觉懂了又觉得没懂真的难受,后来还是大学遇到一位老师(崇拜),她给我指明了道路让系统性的学习了整个前端。带现在才能写出来这个博客。
