    </article>
    <div class="contact">
      <div class="describe">
        <p>有任何问题请联系我吧，收到消息后会给您回复邮件的哦♪(^∇^*)</p>
      </div>
      <el-form
        :model="contactForm"
        :rules="rules"
        ref="contactForm"  
        label-position="top"
      >
        <el-form-item label="你的名字是？" prop="name">
          <el-input v-model="contactForm.name"></el-input>
        </el-form-item>
        <el-form-item label="你的邮箱" prop="email">
          <el-input v-model="contactForm.email"></el-input>
        </el-form-item>
        <el-form-item label="你要发送的消息内容" prop="message">
          <el-input v-model="contactForm.message"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm">发送</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  components: {},
  data(){
    return {
      //推荐文章数据的数组
      recommendArticle:[],
      contactForm:{ //表单的数据对象
        name:"",
        email:"",
        message:""
      },
      rules:{ //表单的验证规则   与标签中的prop属性值对应
        name:[
          {required: true, message:"name值必须填写",trigger:"blur"}
        ],
        email:[
          {required: true, type:"email", trigger:"blur"}
        ],
        message:[
          {required: true, trigger:"blur"}
        ],
      }
    }
  },
  methods:{
    submitForm(){ //点击提交后触发的事件
      //先验证表单里的数据格式是否符合要求
      /* 
        在html标签里写ref属性，比如ref="wanzi"
        在js中，通过this.$refs.wanzi 得到写了ref属性的dom节点。
      */
      // console.log(this.$refs.contactForm); //表单节点对象
      this.$refs.contactForm.validate(async result => {
        if(result){
          //校验通过  发起请求，将数据发送给后端
          let {data} = await this.$axios({
              method:"POST",
              url:"/contact/contact",
              data: this.contactForm
          });
          console.log(data);
          if(data.code) return this.$message.error(data.message);
           this.$message.success("已收到您的反馈，作者会在7个工作日内回复的！"); //回复成功
           this.$refs.contactForm.resetFields();
        }else{
          //未通过校验
          return false;
        }
      });
    },    
  }
};
</script>

