<template>
  <div class="loginuser">
    <el-popover placement="bottom-end" :width="200" trigger="hover">
      <template #reference>
        <img :src="`${baseURL}${userInfo.img}`" alt="" />
      </template>
      <div>
        <el-button type="primary" size="mini" @click="changeInfo"
          >修改信息</el-button
        >
        <el-button type="danger" size="mini" @click="loginOut"
          >退出登录</el-button
        >
      </div>
    </el-popover>
    <span>{{ userInfo.name }}</span>
    <!-- {{baseURL}} -->
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "Avatarname",
  computed: { ...mapState(["userInfo"]) },
  methods: {
    ...mapMutations(["clearUserInfo"]),
    changeInfo() {
      //跳转到/updateUser路由下  编程式导航
      this.$router.push("/userdata");
    },
    async loginOut() {
      let { data } = await this.$axios({
        method: "POST",
        url: "/out",
      });
      this.$message.success(data.message); //退出登录后弹框提示
      this.clearUserInfo(); //清空vuex里的数据
      console.log(this.userInfo);
      //跳转到/路由下  编程式导航
      this.$router.push("/");
    },
  },
};
</script>

<style lang="less" scoped>
.loginuser {
  width: 150px;
  line-height: 50px;
  span{
    display: inline-block;
    line-height: 34px;
    vertical-align: middle;
    color: white;
    height: 100%;
    font-family: '隶书';
  }
  img {
    width: 34px;
    margin-right: 10px;
    vertical-align: middle;
    height: 34px;
    border-radius: 50%;
    cursor: pointer;
  }
}
</style>