import { createStore } from 'vuex'

export default createStore({
  state: {
    userInfo:{}
  },
  mutations: {
    updateUserInfo(state,userInfo){
      state.userInfo = userInfo
    },
    clearUserInfo(state){
      state.userInfo = {}
    },
    updateuserImg(state,str){
      state.userInfo.img = str
    }
  },
  actions: {
  },
  modules: {
  }
})
