import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/admin',
    name: 'admin',
    component: () => import(/* webpackChunkName: "about" */ '../views/Admin.vue'),
  },
  {
    path: '/article',
    name: 'article',
    component: () => import(/* webpackChunkName: "about" */ '../views/Article.vue')
  },
  {
    path: '/message',
    name: 'message',
    component: () => import(/* webpackChunkName: "about" */ '../views/Message.vue')
  },
  {
    path: '/album',
    name: 'album',
    component: () => import(/* webpackChunkName: "about" */ '../views/Album.vue')
  },
  {
    path: '/userdata',
    name: 'userdata',
    component: () => import(/* webpackChunkName: "about" */ '../views/Userdata.vue')
  },
  {
    path: '/article/:id',  //根据文章id加载文章详情组件
    name: 'ArticleDetailes',
    component: () => import('../views/ArticleDetailes.vue')
  },
]
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
