<template>
  <div class="wrap">
    <h2 class="el-icon-coffee">博客信息</h2>
    <ul>
      <li class="count el-icon-coffee-cup">文章总数 <span>{{allArticleData}}</span></li>
      <li class="el-icon-user">最近访客 <span>{{visitorData}}</span> 位</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Bloginfo",
  data(){
    return{
      visitorData:0,
      allArticleData:0
    }
  },
  methods:{
    async getVisitor() {
      let { data } = await this.$axios({
        method: "GET",
        url: "/get/visitor",
      });
      if (data.code) return; //没有访客时直接return
      // console.log(data.data.length );
      this.visitorData = data.data.length;
    },
    async getArticles(){
      let {data} = await this.$axios({
        method:"GET",
        url:"/get/article"
      });
      // console.log(data);
      if(data.code) return this.$message.error(data.message);//没有文章时/报错，直接return
      this.allArticleData = data.data.length; //所有文章数据给allArticleData
    },       
  },
  created(){
    this.getVisitor();
    this.getArticles()
  }
};
</script>

<style lang="less" scoped>
h2 {
  position: relative;
  left: 10px;
}
ul {
  margin-top: 10px;
  li {
    padding-left: 10px;
    &:nth-of-type(1){
        border-bottom: 1px solid #e0e0e0;
    }
  }
}
</style>